:root[data-theme="light"] {
  --background-color: #ffffff;
  --text-color: #2d3b45;
  --border-color: #c7cdd1;
  --button-color: #f5f5f5;
  --accent-color: #d2efff;
}

:root[data-theme="dark"] {
  --background-color: #212121;
  --text-color: #ffffff;
  --border-color: #323232;
  --button-color: #2c2c2c;
  --accent-color: #142f54;
}

@font-face {
  font-family: 'Roboto';
  src: url('fonts/roboto.woff2') format('woff2'),
       url('fonts/roboto.woff') format('woff');
  font-weight: normal;
  font-style: normal;
}

body {
  padding: 30px;
  height: auto;
  min-width: 250px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif Helvetica, sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: all .2s ease;
  background: var(--background-color);
  color: var(--text-color);
}

.ext-icon {
  width: 100px;
  height: 100px;
  margin-bottom: 24px;
}

.ext-buttons, .mode-buttons {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  border-radius: 6px;
  overflow: hidden;

  button {
    flex: 1;
    padding: 8px 12px;
    border: none;
    cursor: pointer;
    text-align: center;
    background: var(--button-color);
    color: var(--text-color);
    transition: background .2s ease;
  }
}

.ext-active, .mode-active {
  background: var(--accent-color) !important;
  color: #fff;
}

.ext-status {
  margin-top: 10px;
  font-size: 12px;
}

.mode-status {
  margin-bottom: 6px;
  font-size: 12px;
}

#ext-off {
  filter: grayscale(1);
}

